import React, { useState } from 'react';
import axios from 'axios';
import { useFormik } from 'formik';
import * as Yup from 'yup';

const Form = () => {
  const [feedback, setFeedback] = useState('');
  const [formClass, setFormClass] = useState('');
  const [buttonAttr, setButtonAttr] = useState(0);

  const formik = useFormik({
     initialValues: {
       firstName: '',
       lastName: '',
       email: '',
     },
     validationSchema: Yup.object({
       firstName: Yup.string()
         .min(5, 'Must be 5 characters or more')
         .required('Required'),
       lastName: Yup.string()
         .min(5, 'Must be 5 characters or less')
         .required('Required'),
       email: Yup.string().email('Invalid email address').required('Required'),
     }),
     onSubmit: values => {
       slackCall(values);
     },
   });

  async function slackCall(values) {
    const webhookUrl = 'https://hooks.slack.com/services/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';

    const data = {
      "text": `First Name: ${values.firstName} \n Last Name: ${values.lastName} \n Email: ${values.email}`,
    }

    let result = await axios.post(webhookUrl, JSON.stringify(data), {
      withCredentials: false,
      transformRequest: [(data, headers) => {
        delete headers.post["Content-Type"]
        return data
      }]
    })

    if (result.status === 200) {
      setFeedback('Your messsage has been sent')
      setFormClass('disabled')
      setButtonAttr(true)
    } else {
      setFeedback('Error')
    }
  }

   return (
     <>
     <form onSubmit={formik.handleSubmit} class={formClass}>
       <label htmlFor="firstName">First Name</label>
       <input
         id="firstName"
         name="firstName"
         type="text"
         onChange={formik.handleChange}
         onBlur={formik.handleBlur}
         value={formik.values.firstName}
       />
       {formik.touched.firstName && formik.errors.firstName ? (
         <div className="warning">{formik.errors.firstName}</div>
       ) : null}
       <label htmlFor="lastName">Last Name</label>
       <input
         id="lastName"
         name="lastName"
         type="text"
         onChange={formik.handleChange}
         onBlur={formik.handleBlur}
         value={formik.values.lastName}
       />
       {formik.touched.lastName && formik.errors.lastName ? (
         <div className="warning">{formik.errors.lastName}</div>
       ) : null}
       <label htmlFor="email">Email Address</label>
       <input
         id="email"
         name="email"
         type="email"
         onChange={formik.handleChange}
         onBlur={formik.handleBlur}
         value={formik.values.email}
       />
       {formik.touched.email && formik.errors.email ? (
         <div className="warning">{formik.errors.email}</div>
       ) : null}
       <button type="submit" disabled={buttonAttr}>Submit</button>
     </form>
     <span className="feedBack">{feedback}</span>
     </>
   );
}

export default Form;